/* css reset */
* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
* {
  padding: 0;
  margin: 0;
}
* {
  list-style: none;
}
a {
  color: inherit;
  text-decoration: none;
}
img {
  width: 150px;
  height: 150px;
}
/* style */
body {
  margin: 319px 0;
  background-image: linear-gradient(125deg,#2c3e50,#27ae60,#2980b9,#e74c3c,#8e44ad);
  background-size:400% 100%;
  animation: bganimation 15s infinite;

}

@keyframes bganimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.globalHeader {
  margin: 20px;
}
@media(min-width:500px){
  .globalHeader{
    margin: 60px 0 100px;
  }
}
.searchForm {
  display: flex;
  justify-content: space-between;
   
}
@media(min-width:500px){
  .searchForm {
  max-width: 400px;
margin-right: auto;
margin-left: auto;}
}

.searchForm > input {
  width: 100%;
  margin-right: 10px;
  height: 30px;
  padding: 0 5px;
  border: none;
  border-radius: 4px;
}
.searchForm > button {
  white-space: nowrap;
  padding: 0 10px;
  border: none;
  border-radius: 4px;
  background: #37a8cc;
  color: white;
  font-size: 16px;
}
.globalMain{
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}
.siteList {
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
  justify-content: space-between;
  
}
@media(min-width:500px){
  .siteList{
    margin-left: 0;
    margin-right: -25px;
    justify-content: flex-start;
  }
}
.siteList > li {
  margin-bottom: 10px;
  
}
@media(min-width:500px){
  .siteList > li{
    margin-right: 25px;
  }
}
.siteList .site {
  border: 1px solid red;
  width: 160px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-color: white;
  border: 1px solid #dddddd;
  border-radius: 4px;
  padding: 20px 0;
  height: 128px;
  position: relative;
  cursor: pointer;
}
.siteList .site > .logo {
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 64px;
  text-transform: uppercase;
}
.siteList .site > .link {
  font-size: 14px;
  margin-top: 4px;
}
.siteList .site > .close {
  position: absolute;
  right: 0;
  top: 0;
  display: none;
  cursor: default;
}
.siteList .site:hover > .close {
display: block;
}

.siteList .addButton {
  border: 1px solid #dddddd;
  width: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px 0;
  height: 128px;
  background-color: white;
}
.siteList .addButton .icon {
  width: 56px;
  height: 56px;
}
.siteList .addButton .text {
  font-size: 14px;
  margin-top: 4px;
}
.siteList .addButton .icon-wrapper {
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sub{
  display: flex;
  justify-content: center;
}
